import React from 'react'
import Popper from '@material-ui/core/Popper';
import Fade from '@material-ui/core/Fade';
import Paper from '@material-ui/core/Paper';
import canvas from './Canvas'
import Draggable from 'react-draggable';
import Config from './Config'

const ShapeLibrary = (props) => {
    const onDragStop = (event, subMenu) => {
        var categ = subMenu.props.category;
        var num = subMenu.props.num;
        var x = canvas.maprx(event.pageX);
        var y = canvas.mapry(event.pageY);
        window.Module.libraryStore().addObjectToCanvas(categ, num, x, y);
        canvas.draw();
    }
    const getSubIcons = (subMenuIcons) =>{
        return subMenuIcons?.map((subMenu,index)=>{
            return (
                <Draggable key={index} position={{x: 0, y: 0}} onStop={(event) => onDragStop(event, subMenu)}>
                  <div style={{display:"inline-block"}}>
                    {subMenu}
                  </div>
                </Draggable>
            )
        })
    }

    var {open, anchorEl, placement, subMenuIcons} = props;
    if (window.matchMedia(Config.mediaQuery).matches) {
        placement = "top";
    }
    return (
        <div>
            <Popper open={open} anchorEl={anchorEl} placement={placement} transition>
                {({ TransitionProps }) => (
                    <Fade {...TransitionProps} timeout={350}>
                        <Paper className="popper_paper poper_paper_res">
                            {getSubIcons(subMenuIcons)}
                         </Paper>
                     </Fade>
                )}
            </Popper>
        </div>
    )
}

export default ShapeLibrary;
